	background: #e0e6ed;
}

.o-button.o-button--outlined:not(.is-toggled):hover::before,
.o-button.o-button--outlined:focus::before {
	opacity: 1;
	-webkit-transform: scale(1);
	transform: scale(1);
}

.o-button.o-button--outlined::before {
	position: absolute;
	top: 0;
	left: 0;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	border-width: 2px;
	border-style: solid;
	content: '';
	opacity: 0;
	transition: opacity .2s, -webkit-transform .2s;
	transition: opacity .2s, transform .2s;
	-webkit-transform: scale(0.8333);
	transform: scale(0.8333);
}

.o-button.o-button--outlined.o-button--warning::before {
	border-color: #e64b3c;
}

