/* BASICS */

.CodeMirror { /* Set height, width, borders, and global font properties here */
	font-family: monospace;
	height: 300px;
}

.CodeMirror-scroll { /* Set scrolling behaviour here */
	overflow: auto;
}

/* PADDING */

.CodeMirror-lines {
	padding: 4px 0; /* Vertical padding around content */
}

.CodeMirror pre {
	padding: 0 4px; /* Horizontal padding of content */
}

.CodeMirror-scrollbar-filler,
.CodeMirror-gutter-filler {
	background-color: white; /* The little square between H and V scrollbars */
}

/* GUTTER */

.CodeMirror-gutters {
	border-right: 1px solid #ddd;
	background-color: #f2f6fa;
	white-space: nowrap;
}

.CodeMirror-linenumber {
	padding: 0 3px 0 5px;
	min-width: 20px;
	text-align: right;
	color: #999;
	box-sizing: content-box;
}

.CodeMirror-guttermarker {
	color: black;
}

.CodeMirror-guttermarker-subtle {
	color: #999;
}

/* CURSOR */

.CodeMirror .CodeMirror-cursor {
	border-left: 1px solid black;
}

/* Shown when moving in bi-directional text */

.CodeMirror .CodeMirror-secondarycursor {
	border-left: 1px solid silver;
}

.CodeMirror.cm-fat-cursor .CodeMirror-cursor {
	width: auto;
	border: 0;
	background: #7e7;
}

.CodeMirror.cm-fat-cursor .CodeMirror-cursors {
	z-index: 1;
}

.cm-animate-fat-cursor {
	width: auto;
	border: 0;
	-webkit-animation: blink 1.06s steps(1) infinite;
	animation: blink 1.06s steps(1) infinite;
}

/* Can style cursor different in overwrite (non-insert) mode */

.cm-tab {
	display: inline-block;
	text-decoration: inherit;
}

.CodeMirror-ruler {
	border-left: 1px solid #ccc;
	position: absolute;
}

